body {
  text-align: center;
  font-family: Arial, sans-serif;
}

h1, .search-form {
  margin-top: 20px;
}

input[type="text"] {
  width: 300px;
  border: 1px solid #ccc;
  padding: 10px;
}

button[type="submit"] {
  color: #fff;
  cursor: pointer;
  background-color: #0074cc;
  border: none;
  padding: 10px 20px;
}

.image-container {
  flex-wrap: wrap;
  justify-content: center;
  margin-top: 20px;
  display: flex;
}

.image-card {
  border: 1px solid #ccc;
  border-radius: 5px;
  margin: 10px;
  padding: 10px;
  box-shadow: 0 2px 4px rgba(0, 0, 0, .1);
}

img {
  max-width: 100%;
  height: auto;
}

.load-more {
  color: #fff;
  cursor: pointer;
  background-color: #007bff;
  border: none;
  border-radius: 5px;
  margin: 20px auto;
  padding: 10px 20px;
  transition: background-color .3s;
  display: block;
}

